import React, {useState} from 'react';
import "../../css/Welcome.css"

function Welcome(props) {
    const [transX, setTransX] = useState(0)
    const [transY, setTransY] = useState(0)

    return (
        <div className={"newWelcomeOuter mainContents welcomeMain "+localStorage.getItem("ColorMode")}
             onMouseMove={(e) => chromaticAberration(e, setTransX, setTransY)}
             onTouchMove={(e) => chromaticAberration(e, setTransX, setTransY)}>
            <div>
                <h1 className={"newWelcome primary "+localStorage.getItem("ColorMode")}
                    style={{transform: `translateY(-50%)`}}>
                    Welcome To My Website<br/>
                    [ETASH TYAGI]
                </h1>
                <h1 className={"newWelcome secondary "+localStorage.getItem("ColorMode")}
                      style={{transform: `translate(${transX}px, calc(${transY}px - 50%))`}}>
                    Welcome To My Website<br/>
                    [ETASH TYAGI]
                </h1>
                <h1 className={"newWelcome tertiary "+localStorage.getItem("ColorMode")}
                      style={{transform: `translate(${-transX}px, calc(${-transY}px - 50%))`}}>
                    Welcome To My Website<br/>
                    [ETASH TYAGI]
                </h1>
            </div>
        </div>
    );
}

function chromaticAberration(e, setTransX, setTransY) {

    if (e._reactName === "onTouchMove") {
        console.log(e.changedTouches[0])
        console.log(window.screen.availWidth)
        setTransX(Math.min(Math.max((e.changedTouches[0].clientX - e.target.clientWidth/2     )/20, -6), 6))
        setTransY(Math.min(Math.max((e.changedTouches[0].screenY - window.screen.availHeight/2)/20, -6), 6))
    } else if (e._reactName === "onMouseMove") {
        setTransX(Math.min(Math.max(e.movementX / 4, -6), 6))
        setTransY(Math.min(Math.max(e.movementY / 4, -6), 6))
    }
}

export default Welcome;
