import React from 'react';
import globalVariables from "../../other/GlobalVariables";

function TerminalInput({ setResult }) {
    return (
        <form className={"terminal input "+localStorage.getItem("ColorMode")}
              onSubmit={e=>submitHandler(e, setResult)}
              onClick={()=>{document.getElementById("termIn").focus()}}>
            <input id={"termIn"}
                   onInput={(e) => keyPressHandler(e, setResult)}/>
        </form>
    );
}

function keyPressHandler(event, setResult) {
    // Implement Trie Search Here
}
function submitHandler(event, setResult) {
    event.preventDefault()
    setResult(globalVariables.termCommands[document.getElementById("termIn").value])
}

export default TerminalInput;
